.leader {
  position: relative;
  margin-bottom: 28px;
}

.leader__inner {
  position: relative;
  padding-bottom: 40px;
}

.leader__photo-link {
  display: block;
  width: 260px;
  margin: 0 auto;
  margin-bottom: 18px;

  @include link-interaction;
  @include transition(opacity, 1s, $ease-out-quart);
}

.leader__link {
  color: $black;

  @include link-interaction;
}

.leader__title {
  margin: 0 0 5px;
  color: $title-color;

  @include font-set(25px, 30px, 700);
}

.leader__label {
  display: block;
  margin-bottom: 24px;
  color: $secondary-color;
  text-transform: uppercase;

  @include font-set(17px, 20px, 700);
}

.leader__description {
  margin: 0 0 28px;
  color: $primary-color;

  @include font-set(15px, 24px);
}

.leader__details {
  margin-bottom: 28px;
}

.leader__price {
  display: block;
  margin-bottom: 26px;
  text-align: center;
  color: $primary-color;

  @include font-set(17px, 24px, 700);
}

.leader__btn {
  width: 100%;
  padding: 15px 35px;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .leader {
    margin-bottom: 50px;
  }

  .leader__inner {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding-bottom: 67px;
  }

  .leader__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 45px;
  }

  .leader__info {
    width: 44%;
  }

  .leader__title {
    margin-left: -4px;

    @include font-set(43px, 50px, 700);
  }

  .leader__label {
    margin: 12px 15px 0;
    max-width: 100px;
    text-align: center;

    @include font-set(17px, 17px, 700);
  }

  .leader__description {
    margin: 0 0 48px;

    @include font-set(17px, 30px);
  }

  .leader__details {
    margin-bottom: 57px;

    @include font-set(15px, 20px);
  }

  .leader__photo-link {
    position: absolute;
    width: 333px;
    height: 404px;
    right: 50px;
    bottom: 65px;
    margin: 0;
  }

  .leader__photo-link img {
    display: block;
    width: 333px;
    height: auto;
  }

  .leader__footer {
    width: 44%;
  }

  .leader__price {
    padding-right: 6px;
    margin-bottom: 34px;

    @include font-set(20px, 24px, 700);
  }

  .leader__btn {
    width: 290px;
  }
}

@media (min-width: $desktop-width) {
  .leader {
    margin-bottom: 60px;
  }

  .leader__inner {
    padding-bottom: 95px;
  }

  .leader__label {
    order: -1;
    margin: 16px 43px 0 31px;

    @include font-set(17px, 17px, 700);
  }

  .leader__header {
    z-index: 2;
    justify-content: flex-start;
  }

  .leader__title {
    @include font-set(53px, 57px, 700);
  }

  .leader__info {
    padding-left: 175px;
  }

  .leader__description {
    margin-bottom: 60px;
  }

  .leader__footer {
    width: 28%;
    margin: 51px 0 0 175px;
  }

  .leader__btn {
    width: 100%;
  }

  .leader__photo-link {
    width: 525px;
    height: 636px;
    z-index: 0;
    bottom: 96px;
  }

  .leader__photo-link img {
    width: 525px;
    height: auto;
  }
}
